<template>
    <el-dialog
        title="添加/修改备注"
        width="50%"
        :visible="true"
        :show-close="false"
    >   
        <div class="dia-con">
            <div class="list">
                <div class="name">备注</div>
                <el-input
                    type="textarea"
                    :rows="2"
                    placeholder="请输入备注内容"
                    v-model="remark"
                />
            </div>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="confirm">确 定</el-button>
        </span>
    </el-dialog>
</template>

<script>
export default {
    props:['data'],
    data() {
        return {
            dialogVisible: false,
            remark: ''
        };
    },
    mounted(){
        this.remark = this.data.remark;
    },
    methods: {
        cancel(){
            this.$emit('cancel', '')
        },
        confirm(){
            this.axios.put(
                "/giftmall/task",
                this.qs.stringify({ id: this.data.id, remark: this.remark }),
                {
                    headers: {
                        "Content-Type": "application/x-www-form-urlencoded",
                        "domain": this.url,
                        "Authorization":sessionStorage.getItem('token')
                    }
                }
            )
            .then((response) => {
                if (response.data.code == 200) {
                    this.$emit('confirm', '')
                } 
            });
        },
    }
};
</script>

<style scoped lang="less">
.dia-con{
    // width: 800px;
    .list{
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0;
        }
        .name{
            text-align: right;
            min-width: 100px;
            margin-right: 10px;
        }
    }
}
</style>



